export default function CareerForm() {
    const {t} = useTranslation();

    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [phoneNumber, setPhoneNumber] = useState("")
    const [about, setAbout] = useState("")
    const [cv, setCv] = useState("")
    const [mletter, setMletter] = useState("")

    // const [submitForm, {loading}] = useMutation(SUBMIT_FORM);

    function handleSubmit(event) {
        event.preventDefault();
        // if (loading) return;
        //
        // submitForm({
        //     variables: {
        //         name: name,
        //         email: email,
        //         phoneNumber: phoneNumber,
        //         about: about,
        //         cv: cv,
        //         mletter: mletter,
        //     }
        // }).then((response) => {
        //     console.log('done', response);
        //     Array.from(document.querySelectorAll("input, textarea")).forEach(
        //         input => (input.value = "")
        //     );
        // }).catch(error => {
        //     console.error(error);
        // })
    }

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '.container',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1500,
            delay: 500
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])

    return (
        <div className="relative pt-5 justify-center ">
            <div className="container mx-auto px-4 h-full flex flex-col justify-center md:items-center pt-24">
                <h1 className="text-center text-2xl md:text-5xl glitch" data-glitch={t('work_for_us')}>
                    {t('work_for_us')}
                </h1>

                <div className="flex flex-col-reverse lg:flex-row sm:pt-0 sm:-mx-4 md:py-4  w-full lg:w-2/4">
                    <form method="post" onSubmit={handleSubmit} className="w-full mb-20 sm:mb-0">
                        <div className="grid grid-cols-1 gap-4">
                            <div>
                                <input className="css--form-style" name="name" type="text" placeholder={t('your_name')} required autoComplete="off" onChange={(e) => {
                                    setName(e.target.value)
                                }}/>
                            </div>
                            <div>
                                <input className="css--form-style" name="email" type="email" placeholder={t('email')} required autoComplete="off" onChange={(e) => {
                                    setEmail(e.target.value)
                                }}/>
                            </div>
                            <div>
                                <input className="css--form-style" name="phone" type="tel" placeholder={t('phone')} autoComplete="off" onChange={(e) => {
                                    setPhoneNumber(e.target.value)
                                }}/>
                            </div>
                        </div>
                        <div className="grid grid-cols-1 gap-4 pt-4">
                            <textarea className="css--form-style" name="about" placeholder={t('about_yourself')} required onChange={(e) => {
                                setAbout(e.target.value)
                            }}/>
                        </div>
                        <div className="grid grid-cols-1 gap-4 pt-4">
                            <div className="css--form-style">
                                <label htmlFor="CVUpload" className="w-full flex"><img className="w-3.5 mr-4" alt="Upload Icon" src="/images/icons/upload-icon.svg"/> {t('your_cv')}</label>
                                <input className="hidden" id="CVUpload" name="cv" type="file" placeholder={t('your_cv')} required autoComplete="off" onChange={(e) => {
                                    setCv(e.target.value)
                                }}/>
                            </div>
                            <div className="css--form-style">
                                <label htmlFor="mletter" className="flex"><img className="w-3.5 mr-4" alt="Upload Icon" src="/images/icons/upload-icon.svg"/>{t('motivational_letter')}</label>
                                <input className="hidden" id="mletter" name="motivational" type="file" placeholder={t('motivational_letter')} required autoComplete="off" onChange={(e) => {
                                    setMletter(e.target.value)
                                }}/>
                            </div>
                        </div>
                        <div className="grid md:grid-cols-4 sm:grid-cols-1 my-4 text-center">
                            <button className="btn px-5" type="submit">{t('submit')}</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}
